.logo {
  all: unset;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 8px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 25px;
  cursor: pointer;
}

.logo img {
  width: 50px;
}

.logo p {
  color: #fff;
  font-weight: 700;
  font-size: 11px;
  width: 150px;
}
