.inner {
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  border-radius: 5px;
  max-width: 600px;
  padding: 40px;
  padding-bottom: 70px;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.8);

  .title {
    margin-bottom: 15px;
    color: rgba(0, 0, 0, 0.75);
    text-align: center;
    span {
      font-weight: 800;
    }
  }

  button {
    margin-top: 30px;
  }
}
