.curve {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: 100px 1fr;
  z-index: -1;

  div:last-child {
    background-image: url(../../assets/curve.svg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}
