.popup {
  position: absolute;
  z-index: 8;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.77);
  transform: translateY(-120%);
  animation: slideDown 0.3s 1 forwards;
}

@keyframes slideDown {
  to {
    transform: translateY(0);
  }
}
