.ul li {
  --width: 20px;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 9px;
}

.ul li::before {
  content: "";
  display: inline-block;
  background-image: url(../../assets/ListStyle.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: var(--width);
  height: var(--width);
}
